.nav{
    position : fixed;
    top: 0;
    padding:  20px;
    width: 100%;
    height:30px;
    z-index: 1;

    /*Animation*/
    transition-timing-function: ease-in;
    transition: all 0.5s;
}


.nav_logo{
    position : fixed;
    top:10px;
    left : 0;
    width:90px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}
.nav_avatar{
    position : fixed;
    right:20px;
    width:40px;
    height:40px;
    cursor: pointer;
}
.nav_contents{
    display : flex ;
    justify-content: space-between;
}
.nav_black{
    background-color: #111;
}